var tools = {
	httpOne: 'https://test.mh.test.laibinwl.cn/api/', // 
	// httpOne: 'http://192.168.1.192:8043/api/', // 
	httpUrl: 'https://test.mh.test.laibinwl.cn', // 
	showtt: function(add1, add2) {
		uni.showToast({
			title: add1,
			duration: 2000,
			icon: add2 ? add2 : 'none'
		})
	},
	showload: function() {
		uni.showLoading({
			mask: true,
		})
	},
	// 
	goNext(gourl) {
		uni.navigateTo({
			url: gourl
		})
	},
	// 
	goBack(num) {
		uni.navigateBack({
			delta: num
		})
	},
	// tabar
	goTab(url) {
		uni.switchTab({
			url: url
		})
	},
	// 
	goAllClose(url) {
		uni.reLaunch({
			url: url
		})
	},
	// 
	goClose(url) {
		uni.redirectTo({
			url: url
		})
	},
	// 
	// method POST GET
	// url
	// data
	//  showLoads
	axios: function(method, url, data, showLoads) {
		if (showLoads) {
			uni.showLoading({
				'title': showLoads
			})
		}; //  showLoads
		return new Promise((resolve, reject) => {
			uni.request({
				url: this.httpOne + url, // url
				method: method, // method POST GET
				data: data, // data
				header: {
					'Content-Type': 'application/json;charset=utf-8'
				},
				success: (res) => {
					if (showLoads) {
						uni.hideLoading()
					}
					resolve(res.data)
				},
				fail: (err) => {
					reject(err)
				},
				complete() {

				}
			})
		})
	},
	//FROM
	// method POST GET
	// url
	// data
	//  showLoads
	axiosFrom: function(method, url, data, showLoads) {
		if (showLoads) {
			uni.showLoading({
				'title': showLoads
			})
		}; //
		return new Promise((resolve, reject) => {
			uni.request({
				url: this.httpOne + url, // url
				method: method, // method POST GET
				data: data, // data
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
				},
				success: (res) => {
					if (showLoads) {
						uni.hideLoading()
					}
					resolve(res.data)
				},
				fail: (err) => {
					reject(err)
				},
				complete() {

				}
			})
		})
	},
	//FROM token
	// method POST GET
	// url
	// data
	//  showLoads
	axiosFromToken: function(method, url, data, showLoads) {
		let token = uni.getStorageSync('token') || ''; //token
		if (showLoads) {
			uni.showLoading({
				'title': showLoads
			})
		}; //
		return new Promise((resolve, reject) => {
			uni.request({
				url: this.httpOne + url, // url
				method: method, // method POST GET
				data: data, // data
				header: {
					'content-Type': 'application/x-www-form-urlencoded',
					'token': token
				},
				success: (res) => {
					if (showLoads) {
						uni.hideLoading()
					}
					resolve(res.data)
				},
				fail: (err) => {
					reject(err)
				},
				complete() {

				}
			})
		})
	},
	// from
	formInfo(data) {
		let show = true
		for (let key in data) {
			const value = data[key]
			if (!value) {
				uni.showToast({
					title: '',
					icon: 'none'
				})

				show = false;
				break;
			}
		}
		return show;
	},

	noPassByMobile(str) {
		if (null != str && str != undefined) {
			var pat = /(\d{3})\d*(\d{4})/;
			return str.replace(pat, '$1****$2');
		} else {
			return "";
		}
	},


	getLiveTimeCount: function(startTime) {
		if (startTime) { //
			this.liveCountTimes = setInterval(() => {
				//ios 2020-02-02 20:20:20  2020/02/02 20:20:20 
				let transedPreTime = startTime.replace(/-/g, '/') ///
				let nowTime = new Date().getTime();
				let preTime = new Date(transedPreTime).getTime()
				let obj = null;
				if (preTime - nowTime > 0) {
					let time = (preTime - nowTime) / 1000;
					let day = parseInt(time / (60 * 60 * 24));
					let hou = parseInt(time % (60 * 60 * 24) / 3600);
					let min = parseInt(time % (60 * 60 * 24) % 3600 / 60);
					let sec = parseInt(time % (60 * 60 * 24) % 3600 % 60);
					obj = {
						day: day < 10 ? '0' + day : day,
						hou: hou < 10 ? '0' + hou : hou,
						min: min < 10 ? '0' + min : min,
						sec: sec < 10 ? '0' + sec : sec
					};
					this.liveCountdown = obj.day + '' + obj.hou + '' + obj.min + '' + obj.sec + ''
					console.log(this.liveCountdown)
				} else {
					obj = {
						day: '00',
						hou: '00',
						min: '00',
						sec: '00'
					};
					this.liveCountdown = obj.day + '' + obj.hou + '' + obj.min + '' + obj.sec + ''
					clearInterval(this.liveCountTimes)
				}
			}, 1000)
		} else {
			this.liveCountdown = ""
		}
	},
	//     
	isSfz(idcard) {
		var id =
			/^[1-9][0-9]{5}(19|20)[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|31)|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}([0-9]|x|X)$/
		if (idcard === '') {
			uni.showToast({
				title: '',
				icon: 'none'
			})
		} else if (!id.test(idcard)) {
			uni.showToast({
				title: '!',
				icon: 'none'
			})
			return false
		} else {
			return false
		}
	},
	//    
	isPhone(val) {
		var patrn = /^(((1[3456789][0-9]{1})|(15[0-9]{1}))+\d{8})$/
		if (!patrn.test(val) || val === '') {
			uni.showToast({
				title: '',
				icon: 'none'
			})
			return false
		} else {
			return true
		}
	},
	// 
	isEmail(email) {
		if (email.search(/^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z0-9]+$/) != -1)
			return true;
		else
			return false;
	},
	// 
	timestampToTime(timestamp) {
		var date = new Date(timestamp); //10*1000131000
		var Y = date.getFullYear() + '-';
		var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
		var D = (date.getDate() < 10 ? '0' + date.getDate() : date.getDate()) + ' ';
		var h = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
		var m = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
		var s = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
		return Y + M + D + h + m + s;
	},
	/**
	 * 
	 * @param str
	 * @returns {Boolean}
	 */
	isChine: function(str) {
		var reg = /^([u4E00-u9FA5]|[uFE30-uFFA0])*$/;
		if (reg.test(str)) {
			return false;
		}
		return true;
	},
	/**/
	tmCheckImage: function(fileName) {
		return /(gif|jpg|jpeg|png|GIF|JPG|PNG)$/ig.test(fileName);
	},
	/**/
	tmCheckVideo: function(fileName) {
		return /(mp4|mp3|flv|wav)$/ig.test(fileName);
	},
	//  6,
	isPassword(val) {
		var reg = /^(?![0-9]+$)(?![a-z]+$)(?![A-Z]+$)(?!([^(0-9a-zA-Z)])+$)^.{8,16}$/;
		if (val === '') {
			uni.showToast({
				title: '',
				icon: 'none'
			})
		} else if (!reg.test(val)) {
			uni.showToast({
				title: '6,',
				icon: 'none'
			})
			return false
		} else {
			return true
		}
	},
}




export default tools;
