// mescroll-body  mescroll-uni 

// import MescrollUni from "./mescroll-uni.vue";
// import MescrollBody from "./mescroll-body.vue";

const MescrollMixin = {
  // components: { // H5mixin, main.js
  // 	MescrollUni,
  // 	MescrollBody
  // },
  data() {
    return {
      mescroll: null, //mescroll,
      imgBaseUrl: this.imgBaseUrl,
      imgBaseUrl1: ''
    }
  },
  //  (down.nativetrue, pagesenablePullDownRefresh:true;mescroll-native)
  onPullDownRefresh() {
    this.mescroll && this.mescroll.onPullDownRefresh()
  },
  // ,, (,, mescroll-body)
  onPageScroll(e) {
    this.mescroll && this.mescroll.onPageScroll(e)
  },
  // , (,, mescroll-body)
  onReachBottom() {
    this.mescroll && this.mescroll.onReachBottom()
  },
  methods: {
    // mescroll,mescroll
    mescrollInit(mescroll) {
      this.mescroll = mescroll
      this.mescrollInitByRef() // 
    },
    // refmescroll (: http://www.mescroll.com/qa.html?v=20200107#q26)
    mescrollInitByRef() {
      if (!this.mescroll || !this.mescroll.resetUpScroll) {
        let mescrollRef = this.$refs.mescrollRef
        if (mescrollRef) this.mescroll = mescrollRef.mescroll
      }
    },
    // 
    downCallback() {
      // mixinresetUpScroll
      this.mescroll.resetUpScroll()
    },
    // 
    upCallback() {
      // mixin500
      setTimeout(() => {
        this.mescroll.endErr()
      }, 500)
    }
  },
  mounted() {
    this.mescrollInitByRef() // , @init@initref
  }
}

export default MescrollMixin
